@use "../globals" as *;

.mobile-menu {

  &__btn {
    width: 30px;
    height: 30px;
    position: relative;
    transition: all 0.5s;
    cursor: pointer;
    z-index: 20;
  
    & span {
      display: block;
      position: absolute;
      width: 30px;
      height: 2px;
      background-color: #333;
      border-radius: 4px;
      transition: all 0.5s;
  
      &:nth-child(1) {
        top: 10px;
      }
  
      &:nth-child(2) {
        bottom: 10px;
      }
    }
  }
}