@use "../globals" as *;
@use "../parts/common" as *;


/*-------------------------------------------
header
-------------------------------------------*/
.header {
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  &__inner {
    
    @include mq(lg) {
      display: flex;
      align-items: center;
    }
  }

  &__ul {
    display: flex;
    align-items: center;
  }

  &__li {
    margin-right: 30px;
    
    &:last-child {
      margin-right: 0;
    }
  }

  &__contact {
    display: none;
    @extend .font-sm;

    @include mq(lg) {
      width: 200px;
      height: 80px;
      line-height: 80px;
      background-color: $cBlack;
      color: $cWhite;
      display: inline-block;
      text-align: center;
    }

    &:hover {

      @include mq(lg) {
        background-color: $cBtnHover;
      }
    }
  }
}


/*-------------------------------------------
Mainvisual
-------------------------------------------*/
#mainvisual {
  @extend .mb-lg;

  & img {
    width: 100%;
    height: calc(100vh - 80px);
    object-fit: cover;
  }
}


/*-------------------------------------------
News
-------------------------------------------*/
.news {
  @extend .mb-lg;

  &__ul {
    
    @include mq(lg) {
      display: flex;
      justify-content: space-between;
    }
  }

  &__li {
    padding: 10px 0;
    margin-bottom: 20px;

    @include mq(lg) {
      width: calc(100%/3);
      border-right: 1px solid $cBlack;
      padding: 10px 20px;
    }
    
    &:first-child {
      
      @include mq(lg) {
        padding-left: 0;
      }
    }
    
    &:last-child {
      margin-bottom: 0;
      
      @include mq(lg) {
        border-right: none;
        padding-right: 0;
      }
    }
  }

  &__date {
    margin-bottom: 16px;

    span {
      width: 50px;
      height: 20px;
      line-height: 20px;
      background-color: $cBlack;
      color: $cWhite;
      display: inline-block;
      @extend .font-sm;
      margin-left: 10px;
      text-align: center;
    }
  }
}

/*-------------------------------------------
About
-------------------------------------------*/
.about {
  @extend .mb-lg;

  @include mq(lg) {
    display: flex;
  }

  &__img {
    margin-bottom: 30px;
  
    @include mq(lg) {
      width: 55%;
      margin-bottom: 0;
    }

    img {
      width: 100%;
      height: 300px;
      object-fit: cover;

      @include mq(lg) {     
        height: 400px;
      }
    }
  }

  &__text {
    padding: 0 16px;
  
    @include mq(lg) {
      width: 45%;
      padding: 180px 5% 0 5%;
    }
  
    p {
      line-height: 2.2;
    }
  }
}


/*-------------------------------------------
Business
-------------------------------------------*/
.business {
  @extend .mb-lg;

  &__flex {

    @include mq(lg) {
      display: flex;
      justify-content: space-between;
      padding: 0 10%;
    }
  }
  
  &__left {
    margin-bottom: 30px;
  
    @include mq(lg) {
      width: 46%;
      margin-top: 100px;
    }
  }
  
  &__right {

    @include mq(lg) {
      width: 46%;
    }
  }
  
  &__item {
    margin-bottom: 30px;
  
    @include mq(lg) {
      margin-bottom: 50px;
    }
  
    &:last-child {
      margin-bottom: 0;
    }
  }
  
  &__title {
    font-weight: bold;
    margin: 0 0 10px 18px;
    position: relative;
  
    &::before {
      content: "";
      width: 8px;
      height: 1px;
      background-color: $cBlack;
      position: absolute;
      top: 50%;
      left: -18px;
    }
  }
}


/*-------------------------------------------
Company
-------------------------------------------*/
.company {

  @include mq(lg) {
    height: 750px;
    display: flex;
    align-items: center;
    position: relative;
  }

  &__text {
    background-color: $cWhite;
    padding: 40px 20px;
    margin-bottom: 20px;
  
    @include mq(lg) {
      width: 55%;
      padding: 100px 8% 100px 6%;
      position: absolute;
      top: 0;
      left: 0;
    }
  }

  &__img {

    @include mq(lg) {
      width: 53%;
      position: absolute;
      top: 115px;
      right: 0;
    }
  
    img {
      width: 100%;
      height: 400px;
      object-fit: cover;
    }
  }  
  
  &__info {
    margin-bottom: 0;
  
    @include mq(lg) {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
    }
  }

  & dt {
    margin-top: 20px;

    @include mq(lg) {
      width: 20%;
      margin-top: 10px;
    }

    &:first-of-type {

      @include mq(lg) {
        margin-top: 0;
      }
    }
  }

  & dd {
    margin-top: 5px;
    padding-left: 14px;

    @include mq(lg) {
      width: 80%;
      margin-top: 10px;
    }

    &:first-of-type {

      @include mq(lg) {
        margin-top: 0;
      }
    }
  }

  &__add {
    margin-left: 20%;
  }
}


/*-------------------------------------------
Footer
-------------------------------------------*/
.footer {
  background-color: $cWhite;
  padding-bottom: 20px;

  &__flex {
    padding: 40px 0;

    @include mq(lg) {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  & .logo {
    margin-right: 0;
    margin-bottom: 10px;

    @include mq(lg) {
      margin: 0;
    }
  }

  &__copyright {
    @extend .pb-sm;
  }
}